<template>
  <div>
    <van-nav-bar title="登录" cross @click-right="$router.push('/register')" right-text="注册"/>
    <van-form @submit="onSubmit">
      <van-field
        v-model="user.phone"
        name="手机号"
        label="手机号"
        placeholder="手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <van-field
        v-model="user.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import {login} from "../util/request"
export default {
  data() {
    return {
      user:{
          phone: "15271093066",
          password: "111",
      }
    };
  },
  methods: {
    onSubmit(values) {
      login(this.user).then(data=>{
           if(data.code==200){
            sessionStorage.setItem("user",JSON.stringify(data.list))
            this.$toast.success('登录成功');
            this.$router.push("/index")
         }else{
             this.$toast.fail('登录失败');
         }
      })
    },
  },
};
</script>

<style scoped>
.van-form {
  padding: 10px;
}
.input {
  background: #fff;
}
.van-field__control {
  background: #fff;
}
</style>


